.global-navbar {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 1% 0 1% 0;
  background-image: url('https://scontent-lhr8-2.xx.fbcdn.net/v/t1.0-9/136755499_2815139832100760_6536541068959030555_o.jpg?_nc_cat=101&ccb=3&_nc_sid=e3f864&_nc_ohc=gWarBD1RIUMAX-kanz_&_nc_ht=scontent-lhr8-2.xx&oh=035d30dda7439206a683ce7012e79c00&oe=60544C75');
  filter: drop-shadow(0 3px 8px black);
}

.global-navbar-divider {
  min-width: 300px;
  width: 60%;
  font-size: 1px;
  background-color: rgba(0,0,0,.5);
  border-radius: 50px;
  filter: drop-shadow(1px 1px 10px black);
}

.logo {
  min-width: 300px;
  width: 60%;
  user-select: none;
  -moz-user-select: none;
  -webkit-user-select: none;
  pointer-events: none;
  filter: drop-shadow(1px 1px 1px black);
}

.menu-buttons {
  display: flex;
  width: 60%;
  min-width: 300px;
  margin: 1% 0 1% 0;
  justify-content: space-between;
  border-radius: 50px;
  background-color: rgba(233,233,233,.75);
}

button {
  background-color: unset;
  border: unset;
  inset: unset;
  color: black;
  width: 100%;
  height: 64px;
  font-size: calc(8px + 2vmin);
  border-top: 3px solid rgb(170, 170, 170, .1);
  border-bottom: 3px solid rgb(170, 170, 170, .1);
  border-radius: 50px;
}

button:focus {
  outline-style: none;
}

button:hover {
  background-color: rgba(233, 233, 233, .25);
  border-top: 3px solid rgb(36, 154, 233);
  border-bottom: 3px solid rgb(36, 154, 233);
}

@media (max-width: 600px) {
  .global-navbar-divider {
    width: 90%;
  }
  .logo {
    width: 90%;
  }
  .menu-buttons {
    width: 90%;
  }
}

@media (min-width: 1000px) {
  .global-navbar-divider {
    width: 60%;
  }
  .logo {
    width: 30%;
  }
  .menu-buttons {
    width: 50%;
  }
}
