
.site-fader {
    top: 0;
    left: 0;
    z-index: 99;
    position: fixed;
    pointer-events: none;
    min-width: 100vmax;
    min-height: 100vmax;
    animation: fadeOut 2s;
}

@keyframes fadeOut {
    0% {
        background-color: rgba(233,233,233,1);
    }
    100% {
        background-color: rgba(233,233,233,0);
    }
}
